* {
  box-sizing: border-box;
}
body {
  margin: 0 auto;
  background: #d0e0e6;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.header, .footer {
  height: 83px;
  background-color: rgb(134, 195, 248);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 50px;
  margin: 0;
  color: rgb(0, 3, 185);
  text-shadow: 1px 1px 2px rgb(40, 36, 78), 0 0 1em rgb(0, 225, 255);
  font-size: 1.6rem;
}
.container {
  margin: 0 auto;
  width: 80%;
  height: calc(100% - (80px * 2));
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  line-height: 1;
}
.cards {
  display: grid;
  grid-template-columns: repeat(4, 150px);
  grid-template-rows: repeat(2, 150px);
  grid-gap: 20px;
}
.picture-card {
  height: 130px;
  width: 130px;
  border-radius: 2px;
  background: #000;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}
.picture-card.flipped .inner {
  transform: rotateY(180deg);
}
.inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: 0.6s cubic-bezier(0.38, 0.02, 0.09, 1.66) all;
}
.picture-card .front,
.picture-card .back {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
}
.picture-card .front {
  background: #fff;
  transform: rotateY(180deg);
}
.picture-card .back {
  background: linear-gradient(90deg, rgb(102, 77, 212), rgb(121, 186, 248), rgb(102, 77, 212));
  font-size: 100px;
  transition: 0.3s ease all;
}
.picture-card .back:hover {
  background: linear-gradient(90deg, rgb(77, 197, 212), rgb(202, 249, 255), rgb(77, 197, 212));
  color: #fff;
  transform: scale(1.02);
}
.modal, .open{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(107, 150, 241, 0.2);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}
.modal-body {
  padding: 2rem;
  width: 50%;
  border-radius: 5px;
  background-color: white;
  height: 400px;
}
.modal-body.win {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.close {
  display: none;
}
.button {
  width: 40px;
  height: 20px;
  border-radius: 5px;
}
.close_btn {
  margin-top: 20px;
  margin-left: 80%;
}
.logo {
  width: 80px;
  height: 50px;
}
a {
  text-decoration: none;
}